.Crud {
    background-image: linear-gradient(135deg,
            rgb(100, 150, 20) 0%,
            rgb(150, 22, 130) 100%);
    padding: 2.3em;
    box-shadow: rgb(150, 22, 100) 2px 0 5px 4px;
    margin: 20px auto;
    width: 80%;
}

.form {
    width: 50%;
    padding: 20px 0;
    border-radius: 20px;
    margin: 20px auto;
    box-shadow: rgb(10, 200, 100) 0 0 5px 1px;
}

.form .attribute {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 20px 0;
    padding: 0 30px;
    width: 90%;
}

.form label {
    text-shadow: rgb(27, 93, 91) 1px 0px 2px;
    margin-bottom: 5px;
    margin-left: 10px;
}

.form .input {
    width: 70%;
    padding: 5px 15px;
    border-radius: 20px;
    background-image: linear-gradient(
        100deg,
        rgb(100,200,180) 1%,
        #fff 100%
    );
    font-size: 14px;
    font-weight: bold;
}
.form .error{
    box-shadow: rgb(250, 100, 100) 0 0 2px 2px;
    border-radius: 10px ;
    padding: 5px 15px ;
    margin: 5px 0 ;
    width: 70%;
    background-image: linear-gradient(
        90deg,#eaa 0%,#faa 100%
    );
}

.form button{
    border-radius: 20px;
    padding: 5px 15px;
    background-image: linear-gradient(
        290deg,#000 0%,#fff 100%
    );
    box-shadow: #fff 0 0 5px 0px;
}
.form button:hover{
    background-image: linear-gradient(
        90deg,#000 0%,#fff 100%
    );
    box-shadow: #000 0 0 5px 0px;
}
.form button:active{
    background-image: linear-gradient(
        290deg,#000 0%,#fff 100%
    );
    box-shadow: #fff 0 0 5px 0px;
}
.view{
    box-shadow: #000 0 0 5px 5px;
    width:76%;
    padding: 20px 10px;
    background-color: #eee;
    margin: 20px auto;
}
.view .sub{
    background-color: #fff;
    border-radius: 20px ;
    border: 2px solid #000;
    margin-bottom: 10px;display: flex;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(
        90deg,#1ff3fe 0%,
        #1f7f3f 50%,
        #1ff34a 100%
    );
}
.view .subject{
    margin-left: 30px;
}
.view .icons{
    margin-right: 30px;
    display: flex;
    justify-content: space-between;
    width: 60px;
    font-size: 25px;
}
 
.delete-icon,.edit-icon{
    cursor: pointer;
    background-color: #ff0;
    width: 40%;
    border-radius: 50px;
    box-shadow: #000 0 0px 10px 1px;
}