.flexbox-container {
    border: 2px solid #000;
 
    height: 300px;
    margin: 20px auto;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.flexbox-item {
    width: 200px;
    height: 200px;
    border: 1px solid #555;
    background-color: #aaa;
    outline: 3px solid #999;
}

.flexbox-item1 {
    min-height: 50px;
    flex-shrink: 0;
    align-self: flex-start;
}

.flexbox-item2 {
    min-height: 100px;
    flex-basis: 0;
    flex-grow: 1;
    align-self: flex-start;
}

.flexbox-item3 {
    min-height: 150px;
}