.App {
  text-align: center;
}


.nav-bar{
  background-color: #555;
  border-radius: 20px;
  padding: 10px 20px;
}
.nav-bar a{
  color: rgb(255,255,255);
  display: inline-block;
  text-decoration: none;
  border: 1px solid #fff;
  padding: 5px 10px;
  border-radius: 1em;

}

.nav-bar>ul{
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width:auto;
}