
.subject1{
    box-shadow: 0 3px 5px 5px #000;
    padding: 20px 30px;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    width: 50%;
    border-radius: 50px;
}
.subject1 >.div{
    display: flex;
    flex-direction: row-reverse;
    margin: 20px auto;
    width: 90%;
}

.subject1 >div>img{
    width: 20vw;
    height: 20vh;
    border-radius: 100%;
    box-shadow: #000 0 5px 5px 5px;
    margin-left: 20px;
}

.subject1 >div >p{
    margin: -1px 10px;
    font-size: 14px;
    font-weight: bold;
    text-align: start;
    text-indent: 25px;
    direction: rtl;    
}
